.card {
  display: flex;
  width: 100%;
  border-radius: 5px;
  overflow: hidden;
  flex-direction: column;
  justify-content: center;
  cursor: pointer;
  border-radius: 10px;
  position: relative;
  flex-basis: 48%;
}

.cardCont {
  width: 100%;
  position: relative;
  height: 25rem;
  border-radius: 10px;
  overflow: hidden;
}
.overlay {
  width: 100%;
  height: 25rem;
  background: linear-gradient(90deg, rgba(0, 0, 0, 0.3), rgba(0, 0, 0, 0.4));
  position: absolute;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  padding: 3rem;
  color: white;
  z-index: 1;
}
.contents {
  width: 100%;
  height: 25rem;
  overflow: hidden;
  transition: 3s ease-in-out;
}
.contents img {
  object-fit: cover;
}

.card:hover .contents {
  transform: scale(1.1);
  position: relative;
}
.card:hover h1 {
  text-decoration: underline;
}

.card h1 {
  font-size: 1.5rem;
}
