@import url("https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400;500;600;700&display=swap");
/* @import url("./styles/auth.css"); */
@import url("./styles/button.css");
@import url("./styles/dashboard.css");
@import url("./styles/user.css");
/* @import url("./styles/store.css"); */
@import url("./styles/table.css");

* {
  font-family: "Montserrat", sans-serif;
  margin: 0;
  padding: 0;
  outline: none;
  box-sizing: border-box;
}

:root {
  --teal: #00a19c;
  --green: #28a745;
}
.mainContainer {
  width: 100%;
  display: flex;
  flex-direction: column;
  /* justify-content: space-between; */
  min-height: 100vh;
  padding: 0 30rem;
}
.topHeader {
  width: 100%;
  z-index: 9999;
}

.sticky {
  position: fixed;
  box-shadow: rgba(149, 157, 165, 0.2) 0px 8px 24px;
}

.publicContents {
  padding: 0.5rem 0;
}

.blogFlex {
  display: flex;
  flex-wrap: wrap;
  gap: 1rem;
}
.marqueeFlex {
  display: flex;
  flex-wrap: wrap;
  gap: 3rem;
}
.blog {
  display: flex;
  flex-direction: column;
  flex: 0.7;
  padding: 2rem;
}
.randomBlog {
  display: flex;
  flex-direction: column;
  flex: 0.3;
  padding: 2rem;
}

.image-gallery-slide img {
  width: 100%;
  height: 30rem !important;
  object-fit: cover;
  overflow: hidden;
  object-position: center center;
}

.formPadding {
  padding: 1rem 2rem;
}

input:focus {
  border: 1px solid rgba(60, 60, 60) !important;
  outline: 1px solid rgba(60, 60, 60) !important;
}
textarea:focus {
  border: 1px solid rgba(60, 60, 60) !important;
  outline: 1px solid rgba(60, 60, 60) !important;
}
select:focus {
  border: 1px solid rgba(60, 60, 60) !important;
  outline: 1px solid rgba(60, 60, 60) !important;
}
.appContainer {
  display: flex;
  flex: 1;
  width: 100%;
  height: 100vh;
  /* background: #f7f8fa; */
  /* background: white; */
  justify-content: space-around;
  background: #f4f7fe;
}

.contentsRight {
  flex-grow: 1;
  padding: 0;
  overflow-y: auto;
  display: flex;
  flex-direction: column;
  flex: 0.87;
  /* background: #f5f5f5; */
  /* background: white; */
  background: #f4f7fe;
}

.dashboardFlex {
  display: flex;
  width: 100%;
  flex-wrap: wrap;
  gap: 1.5rem;
  margin-bottom: 1rem;
}

.activity {
  display: flex;
  flex: 0.25;
  flex-direction: column;
}

.contents {
  display: flex;
  flex-direction: column;
  /* padding: 2rem; */
  /* background: white; */
}

.removePadding {
  display: flex;
  flex-direction: column;
  padding: 0;
}

.courseFlex {
  display: flex;
  /* width: 100%; */
  flex-wrap: wrap;
  gap: 2.5rem;
  margin-bottom: 1rem;
}
.userLogin {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.userLoginCont {
  width: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  border: 1px solid gainsboro;
  padding: 2rem 1rem;
}
.orderContainer {
  display: flex;
  justify-content: space-between;
}
.orderItems {
  display: flex;
  flex: 0.45;
  border: 1px solid gainsboro;
}
.orderForm {
  display: flex;
  flex: 0.45;
  border: 1px solid gainsboro;
}
.shippingForm {
  width: 100%;
  display: flex;
  flex-direction: column;
}
.p10 {
  padding: 1rem 2rem;
}
.inputContainer {
  display: flex;
  flex-direction: column;
  padding: 1rem 2rem;
}
.inputContainer label {
  font-size: 12px;
}
.editorContainer {
  display: flex;
  flex-basis: 100%;
  flex-direction: column;
  padding: 2rem;
}

.editor {
  display: flex;
  flex-basis: 100%;
  flex-direction: column;

  border: 1px solid gainsboro;
  border-radius: 5px;
}
/* Media Query */
@media screen and (min-width: 1600px) and (max-width: 2000px) {
  .mainContainer {
    padding: 0 20rem;
  }
}
@media screen and (min-width: 1300px) and (max-width: 1600px) {
  .mainContainer {
    padding: 0 10rem;
  }
}

@media screen and (max-width: 1300px) {
  .mainContainer {
    padding: 0 7rem;
  }
}

@media screen and (min-width: 1050px) and (max-width: 1300px) {
  .mainContainer {
    padding: 0 5rem;
  }
  /* .contentsRight {
    flex: 1;
  }
  .publicContents {
    padding: 1rem 5rem;
  } */
}

@media screen and (min-width: 700px) and (max-width: 1050px) {
  .mainContainer {
    padding: 0 1rem;
  }
}

@media screen and (max-width: 700px) {
  .mainContainer {
    padding: 0 1rem;
  }
}

@media screen and (max-width: 550px) {
  .mainContainer {
    padding: 0 1rem;
  }
}
