.navigation {
  width: 100%;
  display: flex;
  padding: 0.5rem;
  background: #fff;
  align-items: center;
  justify-content: space-between;
  padding: 0.5rem 2rem;
  border-bottom: 1px solid gainsboro;
}
.navigation p {
  color: white;
}

.logo {
  width: 100%;
  display: flex;
  flex: 0.2;
  height: 4rem;
  color: black;
  align-items: center;
  justify-self: center;
  font-weight: bold;
  text-decoration: none;
}

.logo img {
  width: 10%;
  max-height: 4rem;
  object-fit: contain;
  margin-right: 1rem;
}

.tagContainer {
  width: 100%;
  display: flex;
  flex: 0.3;
  align-items: center;
  justify-content: space-around;
}

.tag {
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 12px;
}

.circle {
  width: 60px;
  height: 60px;
  border: 1px solid #6c920b;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  box-shadow: rgba(149, 157, 165, 0.2) 0px 8px 24px;
  margin-right: 1rem;
  cursor: pointer;
  transition: 3s ease-in-out;
}

.circle:hover {
  background: #6c920b;
}

.circle:hover svg {
  color: white;
}

.pink {
  border: 1px solid #ff3e6d;
}

.pink:hover {
  background: #ff3e6d;
}

.text {
  display: flex;
  flex-direction: column;
}

.text h3 {
  margin-bottom: 0.5rem;
}

.dropdown {
  display: flex;
  align-items: center;
  background: none;
  outline: none;
  border: none;
  font-size: 14px;
  cursor: pointer;
}

.autocomplete {
  display: flex;
  flex-direction: column;
  /* border: 1px solid #999; */
  border-top-width: 0;
  list-style: none;
  margin-top: 3rem;
  /* max-height: 143px; */
  overflow-y: auto;
  padding-left: 0;
  width: 100%;
  position: absolute;
  background: white;
  color: black;
  z-index: 9999;
  /* margin-left: 2rem; */
}

.autocomplete li {
  padding: 8px;
}

.autocomplete > .active,
.autocomplete li:hover {
  background-color: whitesmoke;
  cursor: pointer;
  font-weight: 600;
}

.autocomplete li:not(:last-of-type) {
  /* border-bottom: 1px solid #999; */
}

.inputContainer {
  display: flex;
  flex: 0.3;
  position: relative;
}
.inputContainer input {
  width: 100%;
  height: 3.5rem;
  outline: none;
  padding: 0.5rem 1.5rem;
  border-radius: 10px;
  border: none;
  background: white;
  border: 1px solid gainsboro;
}
.profile {
  display: flex;
  flex: 0.3;
  align-items: center;
  justify-content: flex-end;
}

