.mtn__header {
  display: flex;
  padding: 0.5rem 1rem;
  border-bottom: 1px solid gainsboro;
  justify-content: space-between;
  align-items: center;
  /* margin-bottom: 2rem; */
  background: white;
}
.titleContainer {
  display: flex;
  justify-content: flex-start;
  align-items: center;
}
.titleContainer span {
  display: flex;
  cursor: pointer;
  align-items: center;
  margin-right: 1rem;
}
.dpContainer {
  display: flex;
  justify-content: flex-end;
  align-items: center;
}
.dp {
  width: 50px;
  height: 50px;
  border-radius: 50%;
  background-color: #ffcc00;
  overflow: hidden;
  margin-left: 1rem;
}
.dp img {
  width: 50px;
  height: 50px;
  border-radius: 50%;
}

@media screen and (max-width: 1050px) {
  .mtn__header {
    padding: 0.5rem 3rem;
  }
}

@media screen and (max-width: 700px) {
  .mtn__header {
    padding: 0.5rem 1rem;
  }
}

@media screen and (max-width: 550px) {
  .mtn__header {
    padding: 0.5rem 1rem;
  }
}
