.card {
  width: 100%;
  display: flex;
  align-items: center;
  margin-bottom: 1rem;
  cursor: pointer;
}

.cardImage {
  display: flex;
  flex: 0.3;
}
.cardImage img {
  width: 100%;
  height: 5rem;
  object-fit: cover;
  border-radius: 10px;
}
.cardText {
  display: flex;
  flex-direction: column;
  flex: 0.7;
  padding: 0.5rem 1rem;
}

.cardText h6 {
  font-size: 13px;
}
.cardText span {
  font-size: 11px;
}

.card:hover h6 {
  text-decoration: underline;
}
