.customTable {
    width: 100%;
    display: flex;
    flex-basis: 100%;
    flex-direction: column;
}

.customTable table {
    width: 100%;
    max-width: 100%;
    margin: 10px auto;
    border: none !important;
    border-collapse: collapse
}

.customTable thead th {
    font-weight: 400;
    background: gainsboro;
    color: #000;
    border: none !important;
    outline: none !important;
    margin: 0 !important;

}

.customTable tr {
    background: none;
    border: none !important;
    margin-bottom: 5px;
}



.customTable th,
td {
    text-align: left;
    padding: 20px;
    font-weight: 300;
}