.footerContainer {
  width: 100%;
  display: flex;
  flex-direction: column;
}
.footer {
  display: flex;
  width: 100%;
  background: linear-gradient(180deg, #3d55ef -2500%, #fff 300%);
  padding: 0.5rem 10rem;
  /* min-height: 20rem; */
}

.card {
  display: flex;
  flex-direction: column;
  flex-basis: 25%;
  color: black;
}

.card img {
  width: 20%;
}
.card ul {
  padding: 0;
  margin: 0;
}

.card ul li {
  width: 100%;
  display: flex;
  line-height: 2rem;
}

.card ul li a {
  text-decoration: none;
  color: black;
  font-size: 12px;
  font-weight: 600;
}

.rights {
  display: flex;
  width: 100%;
  padding: 1rem;
  background: #000;
  color: white;
  align-items: center;
  justify-content: space-between;
}

.rights a {
  text-decoration: none;
  color: white;
  margin-right: 1rem;
}
