.carouselFlex {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  gap: 1rem;
  padding: 1rem 2rem;
}

.smallItem {
  display: flex;
  flex-basis: 25%;
  flex-wrap: wrap;
}

.largeItem {
  display: flex;
  flex-basis: 48%;
}

.productFlex {
  display: flex;
  flex-wrap: wrap;
  gap: 2rem;
  padding: 2rem 5rem;
}

.storeFlex {
  display: flex;
  flex-wrap: wrap;
  gap: 1rem;
  /* padding: 2rem 5rem; */
}

.brandContainer {
  display: flex;
  flex-direction: column;
  padding: 2rem 5rem;
}

/* Media Query */

@media screen and (min-width: 1300px) and (max-width: 1600px) {
  .smallItem {
    flex-basis: 24%;
  }
}

@media screen and (max-width: 1300px) {
  .smallItem {
    flex-basis: 24%;
  }
}

@media screen and (min-width: 1050px) and (max-width: 1300px) {
  .smallItem {
    flex-basis: 24%;
  }
}

@media screen and (max-width: 1050px) {
  .smallItem {
    flex-basis: 24%;
  }
}

@media screen and (max-width: 1024px) {
  .smallItem {
    flex-basis: 23%;
  }
}

@media screen and (min-width: 700px) and (max-width: 1050px) {
  .smallItem {
    flex-basis: 100%;
    flex-wrap: nowrap;
    gap: 1rem;
  }

  .largeItem {
    flex-basis: 100%;
  }
}

@media screen and (max-width: 700px) {
  .smallItem {
    flex-basis: 100%;
    flex-wrap: nowrap;
    gap: 1rem;
  }

  .largeItem {
    flex-basis: 100%;
  }
}

@media screen and (max-width: 550px) {
  .carouselFlex {
    flex-direction: column;
  }

  .smallItem {
    flex-direction: column;
    flex-basis: 100%;
  }

  .largeItem {
    flex-basis: 100%;
  }

  .productFlex {
    flex-direction: column;
    padding: 1rem;
  }
}