.card {
  display: flex;
  flex-basis: 46%;
  width: 100%;
  /* height: 30rem; */
  border-radius: 5px;
  overflow: hidden;
  flex-direction: column;
  justify-content: center;
  cursor: pointer;
  background: whitesmoke;
  border-radius: 10px;
}

.cardImage {
  display: flex;
  height: 15rem;
  background: whitesmoke;
  justify-content: flex-end;
  /* border-radius: 20px; */
  overflow: hidden;
}

.cardImage img {
  width: 100%;
  object-fit: cover;
}

.wishlist {
  width: 50px;
  height: 50px;
  background: white;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0.5rem;
  position: absolute;
  border-radius: 50%;
  margin: 1rem;
  cursor: pointer;
  transition: 3s ease-in-out;
}

.wishlist:hover {
  background: rgb(236, 17, 127);
  color: white;
  box-shadow: rgba(0, 0, 0, 0.05) 0px 6px 24px 0px,
    rgba(0, 0, 0, 0.08) 0px 0px 0px 1px;
  transform: scale(1.05);
}

.text {
  display: flex;
  height: 10rem;
  flex-direction: column;
  padding: 0.5rem;
}

.title {
  display: flex;
  justify-content: space-between;
  padding: 0.5rem;
}

.title h6 {
  font-size: 14px;
  font-weight: 600;
}

.name {
  display: flex;
  flex: 0.7;
}

.price {
  display: flex;
  flex: 0.3;
  justify-content: flex-end;
}

.description {
  display: flex;
  padding: 0.5rem;
  font-size: 13px;
}

.btnContainer {
  display: flex;
  justify-content: flex-start;
  padding: 0.5rem;
}

.btnContainer button {
  height: 3rem;
  outline: none;
  padding: 1rem 2rem;
  border-radius: 30px;
  display: flex;
  justify-content: center;
  text-align: center;
  text-decoration: none;
  border: none;
  outline: none;
  align-items: center;
  border: 1px solid black;
  cursor: pointer;
  font-weight: 600;
  background: none;
}

.rating {
  /* padding: 0.5rem; */
  margin: 1rem 0;
  display: flex;
}

.rating span {
  color: #006993;
}

.prop {
  width: 100%;
  display: flex;
  flex-direction: column;
  padding: 0.5rem;
}
.prop p {
  font-size: 12px;
  color: gray;
}

.propCont {
  display: flex;
  justify-content: space-between;
  color: gray;
  align-items: center;
  padding: 1rem 0;
}

.propCont span {
  font-size: 12px;
}
