.carousel {
  width: 100%;
  height: 35rem;
  display: flex;
  padding: 0;
  justify-content: center;
  align-items: center;
  border-radius: 50px;
  overflow: hidden;
  /* color: white; */
}

.carouselWrapper {
  display: flex;
  flex-direction: column;
  flex: 1;
}

.carouselSlide {
  display: flex;
  flex: 1;
}

.carouselSlider {
  display: flex;
  height: 35rem;
  flex: 1;
  flex-direction: column;
  position: relative;
}
.overlay {
  width: 100%;
  height: 35rem;
  background: linear-gradient(90deg, rgba(0, 0, 0, 0.3), rgba(0, 0, 0, 0.4));
  position: absolute;
  z-index: 1;
}
.carouselSlider:hover img {
  transform: scale(1.1);
}
.carouselSlider:hover h1 {
  text-decoration: underline;
}
.carouselSlider img {
  width: 100%;
  height: 35rem;
  object-fit: cover;
  transition: 3s ease-in-out;
}

.carouselImage {
  object-fit: cover;
  object-position: center;
  cursor: pointer;
}

.carouselContent {
  width: 100%;
  display: flex;
  flex-direction: column;
  position: absolute;
  padding: 5rem;
  color: white;
  z-index: 2;
}

.carouselContent h1 {
  font-size: 2rem;
  color: white;
  text-shadow: 0px 0px 5px white;
  cursor: pointer;
}

.carouselContent p {
  color: white;
  line-height: 2rem;
  font-size: 16px;
  text-shadow: 0px 0px 3px var(--blue);
}

.carouselContent span {
  color: white;
}

.carouselControls {
  display: flex;
  position: absolute;
  margin-top: 20rem;
  padding: 1rem 5rem;
  z-index: 2;
}

.carouselControls span {
  width: 30px;
  height: 30px;
  cursor: pointer;
  align-items: center;
  padding: 0.5rem;
  background: whitesmoke;
  color: black;
  border-radius: 8px;
  margin-right: 1rem;
}

/* Media Query */
@media screen and (min-width: 1300px) and (max-width: 1500px) {
  .carouselContent {
    padding: 5rem 15rem;
  }

  .carouselControls {
    padding: 1rem 5rem;
  }
}

@media screen and (max-width: 1300px) {
  .carouselContent {
    padding: 5rem 10rem;
  }

  .carouselControls {
    padding: 1rem 5rem;
  }
}

@media screen and (min-width: 1050px) and (max-width: 1300px) {
  .carouselContent {
    padding: 5rem 10rem;
  }

  .carouselControls {
    padding: 1rem 5rem;
  }
}

@media screen and (max-width: 1050px) {
  .carouselContent {
    padding: 2rem 5rem;
  }

  .carouselControls {
    padding: 1rem 2rem;
  }
}

@media screen and (max-width: 1024px) {
  .carouselContent {
    padding: 2rem 5rem;
  }

  .carouselControls {
    padding: 1rem 2rem;
  }
  .carousel {
    height: 25rem;
    border-radius: 10px;
  }
  .overlay {
    height: 25rem;
  }
  .carouselSlider {
    height: 25rem;
  }
}

@media screen and (max-width: 700px) {
  .carouselContent {
    padding: 2rem;
  }

  .carouselControls {
    padding: 1rem 2rem;
  }
  .carousel {
    height: 25rem;
    border-radius: 10px;
  }
  .overlay {
    height: 25rem;
  }
  .carouselSlider {
    height: 25rem;
  }
}

@media screen and (max-width: 550px) {
  .carouselContent {
    padding: 2rem;
  }

  .carouselControls {
    padding: 1rem;
  }
  .carousel {
    height: 25rem;
    border-radius: 10px;
  }
  .overlay {
    height: 25rem;
  }
  .carouselSlider {
    height: 25rem;
  }
}
