.cardContainer {
  display: flex;
  flex: 48%;
  flex-direction: column;
  background: white;
  padding: 2rem;
  border-radius: 10px;
}
.cardFlex {
  display: flex;
  width: 100%;
  flex-wrap: wrap;
  gap: 2rem;
  margin-bottom: 1rem;
}
.dashboardContainer {
  width: 100%;
  padding: 2rem;
}
.dashCont {
  display: flex;
  flex-wrap: wrap;
  gap: 2rem;
}

.chartContainer {
  display: flex;
  flex: 48%;
  background: white;
  flex-direction: column;
  padding: 2rem;
  border-radius: 10px;
}

.tableProduct {
  display: flex;
  align-items: center;
}
.tableProduct img {
  margin-right: 0.5rem;
}

.bgWhite {
  width: 100%;
  background: white;
  min-height: calc(100vh - 5rem);
  padding: 2rem;
}
