.card {
  width: 100%;
  display: flex;
  flex-basis: 47%;
  align-items: center;
  margin-bottom: 1rem;

  padding: 0.5rem;
  background: white;
}

.cardImage {
  display: flex;
  flex: 0.2;
}
.cardImage img {
  width: 40px;
  height: 40px;
  border-radius: 50%;
}

.text {
  display: flex;
  flex-direction: column;
  flex: 0.8;
  color: gray;
  padding: 0.5rem;
}

.text h4 {
  font-size: 13px;
}
.text p {
  font-size: 11px;
}
